import React from "react";
import { BrowserRouter as Router, Switch,Redirect} from "react-router-dom";
import App from "./App";
const Routing = () => {
  return (
    <>
    <Router>
        <Switch>
            <Redirect to="/" />
        </Switch>
    </Router>
      <App />
    </>
  );
};
export default Routing;
