import React from 'react';
import '../Footer/Footer.css';
import copy from '../../images/copy.svg';
import ReactPixel from 'react-facebook-pixel';
const year = new Date().getFullYear();

ReactPixel.init('2892112397669671', {}, {});

ReactPixel.pageView(); // For tracking page view

const Footer = () => {
return(
    <>
    <div id="footer-container" className="container">
    <p id="footer"><img style={{marginBottom:"3px"}} src={copy} alt=".." /> Purple Bot Inc <span>{year}</span>. All Rights Reserved. / <a href="https://neend.app/privacy.html" id="footer" >Privacy policy</a></p>
    </div>
    </>
)
}
export default Footer;