import React, { useRef, useState, useEffect } from 'react';
import '../MusicPlayer/MusicPlayer.css';
import Preview from '../../images/preview.svg';
import next from '../../images/next.svg';

const MusicPlayer = () => {
  const Playaudio = useRef();
  const Progressbar = useRef();
  const animationRef = useRef();
  const [IsPlaying, setIsPlaying] = useState(false);
  const PlayPause = () => {
    setIsPlaying(!IsPlaying);
    if (!IsPlaying) {
      Playaudio.current.play();
      animationRef.current = requestAnimationFrame(whilePlaying);
    } else {
      Playaudio.current.pause();
    }
  };
  const whilePlaying = () => {
    Progressbar.current.value = Playaudio.current.currentTime;
    animationRef.current = requestAnimationFrame(whilePlaying);
  };
  const ProgressUpdate = () => {
    Playaudio.current.currentTime = Progressbar.current.value;
    console.log(Progressbar.current.value);
  };

  const FrontFifteen = () => {
    Progressbar.current.value = Number(Progressbar.current.value) + 15;
    ProgressUpdate();
  };

  const backFifteen = () => {
    Progressbar.current.value = Number(Progressbar.current.value) - 15;
    ProgressUpdate();
  };
  useEffect(() => {
    window.onload = function () {
      document.getElementById('my_audio').play();
    };
  }, []);
  return (
    <div className="MobileContainer">
      <div className="landscapeImage"></div>
      <div id="text-container">
        <h1 id="Mobile-phone-text">
          What are <span id="span-text">Sleep Stories </span> and how they can
          help you?
        </h1>
      </div>
      <div className="icons">
        <audio
          id="my_audio"
          preload="auto"
          loop="loop"
          ref={Playaudio}
          src="/audio.mp3"
        />
        <button id="play-button">
          <img id="buttonControls" src={Preview} onClick={backFifteen} />
        </button>
        <button id="play-button" onClick={PlayPause}>
          {!IsPlaying ? (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="40"
              height="40"
              fill="#ffff"
              className="bi bi-play-circle-fill"
              viewBox="0 0 16 16"
            >
              <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM6.79 5.093A.5.5 0 0 0 6 5.5v5a.5.5 0 0 0 .79.407l3.5-2.5a.5.5 0 0 0 0-.814l-3.5-2.5z" />
            </svg>
          ) : (
            <svg
              xmlns="http://www.w3.org/2000/svg"
              width="40"
              height="40"
              fill="#ffff"
              className="bi bi-pause-circle-fill"
              viewBox="0 0 16 16"
            >
              <path d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM6.25 5C5.56 5 5 5.56 5 6.25v3.5a1.25 1.25 0 1 0 2.5 0v-3.5C7.5 5.56 6.94 5 6.25 5zm3.5 0c-.69 0-1.25.56-1.25 1.25v3.5a1.25 1.25 0 1 0 2.5 0v-3.5C11 5.56 10.44 5 9.75 5z" />
            </svg>
          )}
        </button>
        <button id="play-button" onClick={FrontFifteen}>
          <img id="buttonControls" src={next} />
        </button>
        <input
          ref={Progressbar}
          type="range"
          min="0"
          max="100"
          defaultValue="0"
          id="duration_slider"
          onChange={ProgressUpdate}
        />
      </div>
    </div>
  );
};
export default MusicPlayer;
